import React, { useState, useRef, useEffect } from 'react';
import { Photo } from '../../types/post.types';
import { Album } from '../../types/album.types';
import { ImageUploader } from '../ImageUploader/ImageUploader';
import { PhotoGrid } from '../PhotoGrid/PhotoGrid';
import { useAuth } from '../../contexts/AuthContext';
import styles from './CreatePostForm.module.css';
import { api } from '../../utils/api';
import { PhotoSelector } from '../PhotoSelector/PhotoSelector';
import { ServerImage } from '../ServerImage/ServerImage';

interface CreatePostFormProps {
    onSuccess?: () => void;
    wallOwnerId?: number;
}

interface AttachmentBase {
    type: 'photo' | 'album';
    id: number;
}

interface PhotoAttachment extends AttachmentBase {
    type: 'photo';
    data: Photo;
}

interface AlbumAttachment extends AttachmentBase {
    type: 'album';
    data: Album;
}

type Attachment = PhotoAttachment | AlbumAttachment;

export const CreatePostForm: React.FC<CreatePostFormProps> = ({ onSuccess, wallOwnerId }) => {
    const { user } = useAuth();
    const [content, setContent] = useState('');
    const [attachments, setAttachments] = useState<Attachment[]>([]);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const [showPhotoSelector, setShowPhotoSelector] = useState(false);
    const [isDragging, setIsDragging] = useState(false);
    const [isExpanded, setIsExpanded] = useState(false);
    const [uploadAlbumId, setUploadAlbumId] = useState<number | null>(null);
    const formRef = useRef<HTMLFormElement>(null);

    useEffect(() => {
        let isSubscribed = true;

        const createUploadAlbum = async () => {
            if (!user) return;
            
            try {
                //    ""
                const response = await api.get(`/albums/user/${user.id}`);
                console.log('    :', response);

                if (!isSubscribed) return;

                //   
                if (!Array.isArray(response)) {
                    throw new Error('   ');
                }

                //   ""
                const albums = response;
                const uploadAlbum = albums.find((album: Album) => album.title === '');
                console.log('  "":', uploadAlbum);
                
                if (uploadAlbum) {
                    console.log('  :', uploadAlbum);
                    if (isSubscribed) {
                        setUploadAlbumId(uploadAlbum.id);
                    }
                } else {
                    console.log('   ""');
                    //      
                    const newAlbumResponse = await api.post('/albums', {
                        title: '',
                        description: '  ',
                        isPrivate: true,
                        userId: user.id
                    });
                    console.log('    :', newAlbumResponse);

                    if (!newAlbumResponse) {
                        throw new Error('      ');
                    }

                    if (isSubscribed) {
                        setUploadAlbumId(newAlbumResponse.id);
                    }
                    console.log('  :', newAlbumResponse);
                }
            } catch (error) {
                if (!isSubscribed) return;

                console.error('  / :', error);
                if (error instanceof Error) {
                    console.error(' :', error.message);
                }
                if (error && typeof error === 'object' && 'response' in error) {
                    const apiError = error as any;
                    console.error('   :', {
                        status: apiError.response?.status,
                        data: apiError.response?.data,
                        headers: apiError.response?.headers
                    });
                }
            }
        };

        createUploadAlbum();

        return () => {
            isSubscribed = false;
        };
    }, [user]);

    //         
    useEffect(() => {
        if (attachments.length > 0) {
            setIsExpanded(true);
        }
    }, [attachments.length]);

    const handleImageUploaded = async (photo: Photo) => {
        try {
            if (!uploadAlbumId) {
                console.error('ID   ');
                return;
            }

            //     
            const currentPhotoCount = attachments.filter(a => a.type === 'photo').length;
            if (currentPhotoCount >= 4) {
                handleUploadError('   4   ');
                return;
            }

            console.log('    :', { 
                photoId: photo.id, 
                albumId: uploadAlbumId,
                photo: photo 
            });
            
            //     ""
            const addToAlbumResponse = await api.post(`/albums/${uploadAlbumId}/photos`, {
                photoIds: [photo.id]
            });
            
            console.log('      :', addToAlbumResponse);

            if (!addToAlbumResponse) {
                throw new Error('        ');
            }

            console.log('    .  UI...');

            const newAttachment: PhotoAttachment = { type: 'photo', id: photo.id, data: photo };
            
            //         
            const updatedAttachments = [...attachments, newAttachment];
            setAttachments(updatedAttachments);
            
            //   expanded,    
            setIsExpanded(true);
            
            //   
            setIsDragging(false);
            
            setError(null);
            
            console.log('    ');
        } catch (err) {
            console.error('     :', err);
            if (err && typeof err === 'object' && 'response' in err) {
                const apiError = err as any;
                console.error('   :', {
                    status: apiError.response?.status,
                    data: apiError.response?.data,
                    headers: apiError.response?.headers
                });
            }
            handleUploadError('     ');
        }
    };

    const handleUploadError = (errorMessage: string) => {
        setError(errorMessage);
    };

    const handleAttachmentDelete = (attachment: Attachment) => {
        setAttachments(prev => prev.filter(a => !(a.type === attachment.type && a.id === attachment.id)));
    };

    const handlePhotosAndAlbumsSelected = (photos: Photo[], albums: Album[]) => {
        //       
        const currentPhotoCount = attachments.filter(a => a.type === 'photo').length;
        const totalPhotos = currentPhotoCount + photos.length;
        
        if (totalPhotos > 4) {
            setError('   4   ');
            return;
        }

        const newAttachments: Attachment[] = [
            ...photos.map(photo => ({ type: 'photo' as const, id: photo.id, data: photo })),
            ...albums.map(album => ({ type: 'album' as const, id: album.id, data: album }))
        ];
        
        //    
        const updatedAttachments = [...attachments, ...newAttachments];
        setAttachments(updatedAttachments);
        
        //   expanded,   
        setIsExpanded(true);
        
        setShowPhotoSelector(false);
        setError(null);
    };

    const handleDragEnter = (e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
        setIsDragging(true);
    };

    const handleDragLeave = (e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();

        // ,     ,      
        const rect = formRef.current?.getBoundingClientRect();
        if (rect) {
            const { clientX, clientY } = e;
            if (
                clientX <= rect.left ||
                clientX >= rect.right ||
                clientY <= rect.top ||
                clientY >= rect.bottom
            ) {
                setIsDragging(false);
            }
        } else {
            setIsDragging(false);
        }
    };

    const handleDragOver = (e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
        //   expanded  
        setIsExpanded(true);
        setIsDragging(true);
    };

    const handleDrop = (e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
        
        //   isDragging ,   ImageUploader   
        //    .     handleImageUploaded
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        
        if (!user) {
            setError('   ');
            return;
        }

        if (!content.trim() && attachments.length === 0) {
            setError('    ');
            return;
        }

        setIsSubmitting(true);
        setError(null);

        try {
            const photoAttachments = attachments.filter((a): a is PhotoAttachment => a.type === 'photo');
            const albumAttachments = attachments.filter((a): a is AlbumAttachment => a.type === 'album');
            
            console.log(' :', {
                content: content.trim(),
                photoIds: photoAttachments.map(a => a.id),
                albums: albumAttachments.map(a => a.id),
                authorId: user.id,
                wallOwnerId
            });
            
            //     ,        
            const endpoint = wallOwnerId ? '/wall/posts' : '/posts';
            
            //    
            const formData = new FormData();
            formData.append('content', content.trim());
            formData.append('authorId', user.id.toString());
            
            if (wallOwnerId) {
                formData.append('wallOwnerId', wallOwnerId.toString());
            }
            
            //  ID   
            if (albumAttachments.length > 0) {
                albumAttachments.forEach(album => {
                    formData.append('albums[]', album.id.toString());
                });
            }
            
            //  ,     
            // (         "")
            //  ,      
            
            const response = await api.post(endpoint, formData);
            
            console.log('    :', response);
            
            setContent('');
            setAttachments([]);
            onSuccess?.();
        } catch (err) {
            console.error('   :', err);
            setError(err instanceof Error ? err.message : ' ');
        } finally {
            setIsSubmitting(false);
        }
    };

    const handleTextareaFocus = () => {
        setIsExpanded(true);
    };

    const handleTextareaChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        setContent(e.target.value);
        //         
        setIsExpanded(true);
    };

    if (!user) {
        return (
            <div className={styles.error}>
                      
            </div>
        );
    }

    const photoAttachments = attachments.filter((a): a is PhotoAttachment => a.type === 'photo');
    const albumAttachments = attachments.filter((a): a is AlbumAttachment => a.type === 'album');
    const hasContent = content.trim().length > 0 || attachments.length > 0;

    return (
        <form 
            className={`${styles.form} ${isDragging ? styles.dragging : ''}`}
            onSubmit={handleSubmit}
            ref={formRef}
            onDragEnter={handleDragEnter}
            onDragLeave={handleDragLeave}
            onDragOver={handleDragOver}
            onDrop={handleDrop}
        >
            <div className={styles.textareaContainer}>
                <textarea
                    className={styles.textarea}
                    value={content}
                    onChange={handleTextareaChange}
                    onFocus={handleTextareaFocus}
                    placeholder={wallOwnerId ? " -  ..." : "   ?"}
                    rows={isExpanded ? 4 : 1}
                />
            </div>

            {isDragging && (
                <ImageUploader
                    onImageUploaded={handleImageUploaded}
                    onError={handleUploadError}
                />
            )}

            {isExpanded && (
                <>
                    {photoAttachments.length > 0 && (
                        <div className={styles.preview}>
                            <PhotoGrid 
                                photos={photoAttachments.map(a => a.data)}
                                onPhotoDelete={(photo) => handleAttachmentDelete({ type: 'photo', id: photo.id, data: photo })}
                                canDelete={true}
                                isEditing={true}
                            />
                        </div>
                    )}

                    {albumAttachments.length > 0 && (
                        <div className={styles.albumsPreview}>
                            {albumAttachments.map(attachment => (
                                <div key={attachment.id} className={styles.albumPreviewItem}>
                                    <div className={styles.albumCover}>
                                        {attachment.data.photos[0] && (
                                            <ServerImage
                                                path={attachment.data.photos[attachment.data.photos.length - 1].path}
                                                alt={attachment.data.title}
                                                className={styles.albumCoverImage}
                                            />
                                        )}
                                        <button
                                            type="button"
                                            className={styles.deleteButton}
                                            onClick={() => handleAttachmentDelete(attachment)}
                                            title=" "
                                        />
                                    </div>
                                    <div className={styles.albumInfo}>
                                        <div className={styles.albumTitle}>{attachment.data.title}</div>
                                        <div className={styles.albumCount}>
                                            {attachment.data.photosCount} 
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    )}
                </>
            )}

            <div className={styles.mediaSection}>
                <button
                    type="button"
                    className={styles.photoSelectorButton}
                    onClick={() => setShowPhotoSelector(true)}
                >
                    
                </button>

                {hasContent && (
                    <button
                        type="submit"
                        className={styles.submitButton}
                        disabled={isSubmitting || (!content.trim() && attachments.length === 0)}
                    >
                        {isSubmitting ? '...' : ''}
                    </button>
                )}
            </div>

            {showPhotoSelector && (
                <div className={styles.photoSelectorOverlay}>
                    <PhotoSelector
                        userId={user.id}
                        onSelect={handlePhotosAndAlbumsSelected}
                        onCancel={() => setShowPhotoSelector(false)}
                        multiple={true}
                    />
                </div>
            )}

            {error && (
                <div className={styles.error}>
                    {error}
                </div>
            )}

            {isExpanded && (
                <div className={styles.footer}>
                    <span className={styles.attachmentCount}>
                        {attachments.length > 0 && (
                            <>
                                {photoAttachments.length > 0 && `${photoAttachments.length} `}
                                {photoAttachments.length > 0 && albumAttachments.length > 0 && ', '}
                                {albumAttachments.length > 0 && `${albumAttachments.length} ${albumAttachments.length > 1 ? '' : ''}`}
                            </>
                        )}
                    </span>
                </div>
            )}
        </form>
    );
}; 