import React, { useEffect, useState, useRef } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { Spinner } from '../components/Spinner/Spinner';
import styles from './MusicPage.module.css';
import { Track as DemoTrack, getDemoTracks } from './demoTracks';
import AuPlayerWrap from '../components/AuPlayer/wrap/AuPlayWrap';
import AuOrder from '../components/AuPlayer/AuOrder';
import { usePlayer } from '../contexts/PlayerContext';
import UploadAudio from '../components/UploadAudio';
//  URL API   
const API_URL = process.env.REACT_APP_API_URL || 'https://blue-bikes-happen.loca.lt/api';
const MEDIA_URL = process.env.REACT_APP_MEDIA_URL || 'https://blue-bikes-happen.loca.lt/api/media';

interface Track {
    id: number;
    title: string;
    artist: string;
    duration: string;
    coverUrl: string;
    audioUrl: string;
    playCount: number;
}

interface PaginationInfo {
    total: number;
    page: number;
    limit: number;
    pages: number;
    hasMore: boolean;
}

//   
enum TabType {
    MyMusic = 'my-music',
    Queue = 'queue'
}

export const MusicPage: React.FC = () => {
    const { user } = useAuth();
    const [currentTrack, setCurrentTrack] = useState<Track | null>(null);
    const [isPlaying, setIsPlaying] = useState(false);
    const [tracks, setTracks] = useState<Track[]>([]);
    const [isLoading, setIsLoading] = useState(true);
    const [debugInfo, setDebugInfo] = useState<string | null>(null);
    const [debugVisible, setDebugVisible] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const [uploadingTrack, setUploadingTrack] = useState(false);
    const [uploadProgress, setUploadProgress] = useState(0);
    const [coverError, setCoverError] = useState(false);
    const [activeTab, setActiveTab] = useState<TabType>(TabType.MyMusic);
    const [expandedView, setExpandedView] = useState(false);
    const audioRef = useRef<HTMLAudioElement>(null);
    const [pagination, setPagination] = useState<PaginationInfo>({
        total: 0,
        page: 1,
        limit: 20,
        pages: 0,
        hasMore: false
    });
    const [isLoadingMore, setIsLoadingMore] = useState(false);
    const { 
        playTrack, 
        currentTrack: playerTrack, 
        isPlaying: playerIsPlaying, 
        getTrackCover,
        tracks: queueTracks,
        setTracks: setQueueTracks,
        addToQueue
    } = usePlayer();

    useEffect(() => {
        fetchTracks(1, true);
    }, []);

    useEffect(() => {
        //        
        const handleScroll = () => {
            if (activeTab !== TabType.MyMusic || !pagination.hasMore || isLoadingMore || isLoading) {
                return;
            }

            // ,    
            const scrollTop = window.scrollY || document.documentElement.scrollTop;
            const windowHeight = window.innerHeight;
            const documentHeight = document.documentElement.scrollHeight;
            
            //       ( 300px  )
            if (documentHeight - scrollTop - windowHeight < 300) {
                loadMoreTracks();
            }
        };

        //     
        window.addEventListener('scroll', handleScroll);

        return () => {
            //    
            window.removeEventListener('scroll', handleScroll);
        };
    }, [activeTab, pagination.hasMore, isLoadingMore, isLoading, pagination.page]);

    useEffect(() => {
        if (currentTrack && audioRef.current) {
            try {
                console.log('[Music]   :', currentTrack.audioUrl);
                audioRef.current.src = currentTrack.audioUrl;
                
                audioRef.current.onerror = ((e: Event) => {
                    console.error('  :', e);
                    const audioElement = audioRef.current;
                    if (audioElement && audioElement.error) {
                        console.error(' :', audioElement.error.code);
                        console.error(' :', audioElement.error.message);
                        
                        //      
                        let errorMessage = '   : ' + currentTrack.title;
                        switch (audioElement.error.code) {
                            case MediaError.MEDIA_ERR_ABORTED:
                                errorMessage += ' ( )';
                                break;
                            case MediaError.MEDIA_ERR_NETWORK:
                                errorMessage += ' ( )';
                                break;
                            case MediaError.MEDIA_ERR_DECODE:
                                errorMessage += ' ( )';
                                break;
                            case MediaError.MEDIA_ERR_SRC_NOT_SUPPORTED:
                                errorMessage += ' (  )';
                                break;
                        }
                        
                        alert(errorMessage);
                    } else {
                        alert('   : ' + currentTrack.title);
                    }
                    setIsPlaying(false);
                }) as OnErrorEventHandler;
                
                if (isPlaying) {
                    const playPromise = audioRef.current.play();
                    
                    if (playPromise !== undefined) {
                        playPromise.catch(error => {
                            console.error('[Music]  :', error);
                            setIsPlaying(false);
                        });
                    }
                } else {
                    audioRef.current.pause();
                }
            } catch (error) {
                console.error('[Music]    :', error);
                setIsPlaying(false);
            }
        }
    }, [currentTrack, isPlaying]);

    useEffect(() => {
        setCoverError(false);
    }, [playerTrack?.id]);

    const handleBackgroundCoverError = () => {
        console.warn('[MusicPage]    :', playerTrack?.coverUrl);
        setCoverError(true);
    };

    const fetchTracks = (page = 1, resetTracks = false) => {
        if (resetTracks) {
            setTracks([]);
            setError(null);
        }
        
        setIsLoading(resetTracks);
        setIsLoadingMore(page > 1);
        
        fetch(`${API_URL}/music?page=${page}&limit=${pagination.limit}`, {
            headers: {
                'Accept': 'application/json'
            },
            credentials: 'include'
        })
            .then(response => {
                if (!response.ok) {
                    throw new Error(`HTTP : ${response.status}`);
                }
                return response.json();
            })
            .then(data => {
                console.log('[Music]  :', data);
                
                //  
                const validatedTracks = data.tracks.map((track: any) => {
                    const validTrack: Track = {
                        id: track.id || 0,
                        title: track.title || ' ',
                        artist: track.artist || ' ',
                        duration: track.duration || 0,
                        coverUrl: track.coverUrl || '/api/music/cover/default.png',
                        //       
                        audioUrl: track.filename ? `${API_URL}/music/file/${track.filename}` : '',
                        playCount: track.playCount || 0
                    };
                    
                    //    URL
                    if (!validTrack.audioUrl) {
                        console.warn(`[Music]  ${validTrack.title} (ID: ${validTrack.id})    URL`);
                    }
                    
                    //   
                    if (validTrack.coverUrl && validTrack.coverUrl !== '/api/music/cover/default.png') {
                        //     
                        const img = new Image();
                        img.onerror = () => {
                            console.warn(`[Music]     ${validTrack.title}: ${validTrack.coverUrl}`);
                            //         
                            setTracks(current => 
                                current.map(t => 
                                    t.id === validTrack.id ? { ...t, coverUrl: '/api/music/cover/default.png' } : t
                                )
                            );
                        };
                        img.src = validTrack.coverUrl;
                    }
                    
                    return validTrack;
                });
                
                //   
                if (resetTracks) {
                    setTracks(validatedTracks);
                } else {
                    setTracks(prevTracks => [...prevTracks, ...validatedTracks]);
                }
                
                //    
                setPagination(data.pagination);
            })
            .catch(err => {
                console.error('[Music]    :', err);
                setError(`   : ${err.message}`);
            })
            .finally(() => {
                setIsLoading(false);
                setIsLoadingMore(false);
            });
    };
    
    //     
    const loadMoreTracks = () => {
        if (pagination.hasMore && !isLoadingMore) {
            const nextPage = pagination.page + 1;
            console.log(`[Music]   ,  ${nextPage}`);
            fetchTracks(nextPage, false);
        }
    };

    //       " "
    const handleSelectTrack = async (track: Track) => {
        console.log('[Music]    :', track);
        
        try {
            //      
            setIsLoading(true);
            
            const response = await fetch(`${API_URL}/music?limit=1000`, {
                headers: {
                    'Accept': 'application/json'
                },
                credentials: 'include'
            });
            
            if (!response.ok) {
                throw new Error(`HTTP : ${response.status}`);
            }
            
            const data = await response.json();
            console.log('[Music]     :', data);
            
            //      
            const allTracks = data.tracks.map((trackData: any) => {
                return {
                    id: trackData.id || 0,
                    title: trackData.title || ' ',
                    artist: trackData.artist || ' ',
                    duration: trackData.duration || '0:00',
                    coverUrl: trackData.coverUrl || '/api/music/cover/default.png',
                    audioUrl: trackData.filename ? `${API_URL}/music/file/${trackData.filename}` : '',
                    playCount: trackData.playCount || 0
                };
            });
            
            console.log(`[Music]     ${allTracks.length} `);
            
            //   
            setQueueTracks([...allTracks]);
            
            //      
            const selectedTrack = allTracks.find((t: Track) => t.id === track.id) || track;
            
            //  
            console.log('[Music]   :', selectedTrack);
            playTrack(selectedTrack);
            
        } catch (error) {
            console.error('[Music]       :', error);
            //   ,    
            setQueueTracks([...tracks]);
            playTrack(track);
        } finally {
            setIsLoading(false);
        }
    };

    //        
    const handleAddToQueue = (track: Track, e: React.MouseEvent) => {
        e.stopPropagation(); //     
        
        // ,       
        const trackExists = queueTracks.some(t => t.id === track.id);
        
        if (!trackExists) {
            console.log('[Music]    :', track);
            addToQueue(track);
                } else {
            console.log('[Music]    :', track);
        }
    };

    const handleDeleteTrack = async (trackId: number, e: React.MouseEvent) => {
        e.stopPropagation(); //     
        
        if (!window.confirm(' ,     ?')) {
            return;
        }
        
        try {
            const response = await fetch(`${API_URL}/music/${trackId}`, {
                method: 'DELETE',
                headers: {
                    'Accept': 'application/json'
                },
                credentials: 'include'
            });
            
            if (response.ok) {
                //     
                setTracks(prev => prev.filter(track => track.id !== trackId));
                
                //     ,    
                setQueueTracks(prev => prev.filter(track => track.id !== trackId));
                
                //    
                setPagination(prev => ({
                    ...prev,
                    total: Math.max(0, prev.total - 1),
                    pages: Math.max(1, Math.ceil((prev.total - 1) / prev.limit))
                }));
                
                // ,     
                if (tracks.length < 5 && pagination.hasMore) {
                    loadMoreTracks();
                }
            } else {
                console.error('   ');
            }
        } catch (error) {
            console.error('   :', error);
        }
    };

    //      
    const handleDeleteAllTracks = async (e: React.MouseEvent) => {
        e.stopPropagation();
        
        // ,     
        if (pagination.total === 0 || tracks.length === 0) {
            alert('     ');
            return;
        }
        
        //    
        const confirmation = window.confirm(` ,      (${pagination.total})?    .`);
        
        if (!confirmation) {
            return;
        }
        
        try {
            const response = await fetch(`${API_URL}/music/user/all`, {
                method: 'DELETE',
                credentials: 'include',
                headers: {
                    'Accept': 'application/json'
                }
            });
            
            if (!response.ok) {
                throw new Error(` HTTP: ${response.status}`);
            }
            
            const result = await response.json();
            console.log('[Music]    :', result);
            
            //   
            setTracks([]);
            
            //  
            setPagination({
                total: 0,
                page: 1,
                limit: pagination.limit,
                pages: 0,
                hasMore: false
            });
            
            //     
            alert(` ${result.deletedCount} `);
            
        } catch (error) {
            console.error('[Music]     :', error);
            alert(`   : ${error instanceof Error ? error.message : ' '}`);
        }
    };

    //       
    const handleTrackUploaded = (newTrack: Track) => {
        console.log('[Music]   :', newTrack);
        
        //     
        setTracks(prev => [newTrack, ...prev]);
        
        //    
        setPagination(prev => ({
            ...prev,
            total: prev.total + 1,
            pages: Math.ceil((prev.total + 1) / prev.limit)
        }));
    };

    //  " "
    const renderMyMusicTab = () => {
        return (
            <div className={`${styles.myMusicTab} ${expandedView ? styles.expanded : ''}`}>
                <div className={styles.myMusicHeader}>
                    <h2> </h2>
                    <div className={styles.myMusicActions}>
                        <button 
                            className={styles.deleteAllButton} 
                            onClick={handleDeleteAllTracks}
                            title="  "
                            disabled={isLoading || tracks.length === 0}
                        >
                              
                        </button>
                    </div>
                </div>
                
                <div className={styles.trackListContainer}>
                    {isLoading && pagination.page === 1 ? (
                        <div className={styles.loading}>
                            <Spinner />
                            <p>  ...</p>
                        </div>
                    ) : error ? (
                        <div className={styles.errorMessage}>
                            {error}
                        </div>
                    ) : tracks.length === 0 ? (
                        <div className={styles.emptyState}>
                            <p>     </p>
                            <p>   "+"    ,    </p>
                        </div>
                    ) : (
                        <div className={styles.trackList}>
                            <div className={styles.trackListHeader}>
                                <div className={styles.trackCount}>
                                    : {pagination.total} {pagination.total === 1 ? '' : 
                                              pagination.total < 5 ? '' : ''}
                                </div>
                            </div>
                            
                            <div className={styles.modernTrackList}>
                                {tracks.map((track, index) => {
                                    const isCurrentTrack = playerTrack && playerTrack.id === track.id;
                                    const coverUrl = track.coverUrl;
                                    
                                    return (
                                        <div 
                                            key={track.id} 
                                            className={`${styles.modernTrackItem} ${isCurrentTrack ? styles.activeTrack : ''}`}
                                        >
                                            <div 
                                                className={styles.trackMainInfo}
                                                onClick={() => handleSelectTrack(track)}
                                            >
                                                <div className={styles.trackCoverContainer}>
                                                    <img 
                                                        src={coverUrl} 
                                                        alt={track.title} 
                                                        className={styles.modernTrackCover}
                                                        onError={(e) => {
                                                            (e.target as HTMLImageElement).src = '/api/music/cover/default.png';
                                                        }}
                                                    />
                                                    {isCurrentTrack && playerIsPlaying ? (
                                                        <div className={styles.playingOverlay}>
                                                            <div className={styles.pauseIcon}>
                                                                <span></span>
                                                                <span></span>
                                                            </div>
                                                        </div>
                                                    ) : (
                                                        <div className={styles.playOverlay}>
                                                            <div className={styles.playIcon}>
                                                                <svg viewBox="0 0 24 24" width="24" height="24" fill="white">
                                                                    <path d="M8 5v14l11-7z"/>
                                                                </svg>
                                                            </div>
                                                        </div>
                                                    )}
                                                </div>
                                                
                                                <div className={styles.trackMetadata}>
                                                    <div className={styles.modernTrackTitle}>
                                                        {track.title}
                                                    </div>
                                                    <div className={styles.modernTrackArtist}>
                                                        {track.artist}
                                                    </div>
                                                </div>
                                            </div>
                                            
                                            <div className={styles.trackSecondaryInfo}>
                                                <div className={styles.modernTrackDuration}>
                                                    {track.duration}
                                                </div>
                                                
                                                <div className={styles.modernTrackActions}>
                                                    <button 
                                                        className={styles.queueButton}
                                                        onClick={(e) => handleAddToQueue(track, e)}
                                                        title="  "
                                                    >
                                                        <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                                                            <path d="M14 10H2v2h12v-2zm0-4H2v2h12V6zm4 8v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zM2 16h8v-2H2v2z"/>
                                                        </svg>
                                                    </button>
                                                    <button 
                                                        className={styles.deleteButton}
                                                        onClick={(e) => handleDeleteTrack(track.id, e)}
                                                        title=" "
                                                    >
                                                        <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                                                            <path d="M6 19c0 1.1.9 2 2 2h8c1.1 0 2-.9 2-2V7H6v12zM8 9h8v10H8V9zm7.5-5l-1-1h-5l-1 1H5v2h14V4h-3.5z"/>
                                                        </svg>
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                    );
                                })}
                            </div>
                            
                            {/*     */}
                            {isLoadingMore && (
                                <div className={styles.loadingMore}>
                                    <Spinner />
                                    <p> ...</p>
                                </div>
                            )}
                            
                            {/*    */}
                            {!isLoadingMore && !pagination.hasMore && tracks.length > 0 && (
                                <div className={styles.endOfList}>
                                    <p>    </p>
                                </div>
                            )}
                        </div>
                    )}
                </div>
            </div>
        );
    };
    
    //  ""
    const renderQueueTab = () => {
        if (queueTracks.length === 0) {
            return (
                <div className={styles.emptyState}>
                    <p>  </p>
                    <p>    " "</p>
                </div>
            );
        }
        
        return (
            <div className={styles.queueContainer}>
                <div className={styles.queueHeader}>
                    <div className={styles.trackCount}>
                         : {queueTracks.length} {queueTracks.length === 1 ? '' : 
                                   queueTracks.length < 5 ? '' : ''}
                    </div>
                </div>
                
                <div className={styles.modernTrackList}>
                    {queueTracks.map((track, index) => {
                        const isCurrentTrack = playerTrack && playerTrack.id === track.id;
                        const coverUrl = track.coverUrl;
                        
                        return (
                            <div 
                                key={`queue-${track.id}-${index}`} 
                                className={`${styles.modernTrackItem} ${isCurrentTrack ? styles.activeTrack : ''}`}
                            >
                                <div 
                                    className={styles.trackMainInfo}
                                    onClick={() => playTrack(track)}
                                >
                                    <div className={styles.trackCoverContainer}>
                                        <img 
                                            src={coverUrl} 
                                            alt={track.title} 
                                            className={styles.modernTrackCover}
                                            onError={(e) => {
                                                (e.target as HTMLImageElement).src = '/api/music/cover/default.png';
                                            }}
                                        />
                                        {isCurrentTrack && playerIsPlaying ? (
                                            <div className={styles.playingOverlay}>
                                                <div className={styles.pauseIcon}>
                                                    <span></span>
                                                    <span></span>
                                                </div>
                                            </div>
                                        ) : (
                                            <div className={styles.playOverlay}>
                                                <div className={styles.playIcon}>
                                                    <svg viewBox="0 0 24 24" width="24" height="24" fill="white">
                                                        <path d="M8 5v14l11-7z"/>
                                                    </svg>
                                                </div>
                                            </div>
                                        )}
                                    </div>
                                    
                                    <div className={styles.trackMetadata}>
                                        <div className={styles.modernTrackTitle}>
                                            {track.title}
                                        </div>
                                        <div className={styles.modernTrackArtist}>
                                            {track.artist}
                                        </div>
                                    </div>
                                </div>
                                
                                <div className={styles.trackSecondaryInfo}>
                                    <div className={styles.modernTrackDuration}>
                                        {track.duration}
                                    </div>
                                    
                                    <div className={styles.modernTrackActions}>
                                        <button 
                                            className={styles.deleteButton}
                                            onClick={(e) => {
                                                e.stopPropagation();
                                                //    
                                                setQueueTracks(prev => prev.filter((t, i) => i !== index));
                                            }}
                                            title="  "
                                        >
                                            <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                                                <path d="M19 13H5v-2h14v2z"/>
                                            </svg>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        );
                    })}
                </div>
            </div>
        );
    };

    //      
    const toggleViewMode = () => {
        setExpandedView(!expandedView);
    };

    //  URL   
    const coverUrl = playerTrack ? (coverError ? '/api/music/cover/default.png' : getTrackCover(playerTrack.coverUrl)) : '';

    //      ,   
    if (isLoading && tracks.length === 0 && queueTracks.length === 0) {
        return (
            <div className={styles.centeredContainer}>
                <div className={styles.loading}>
                    <Spinner />
                    <p> ...</p>
                </div>
            </div>
        );
    }

    return (
        <div className={`${expandedView ? styles.expandedMode : ''}`}>
        <div className={styles.container}>
                {playerTrack && (
                    <>
                        <div 
                            className={styles.backgroundCover} 
                            style={{ 
                                backgroundImage: `url(${coverUrl})`,
                                opacity: playerIsPlaying ? 0.3 : 0.15
                            }}
                        />
                        {/*       */}
                        <img 
                            src={playerTrack.coverUrl} 
                            onError={handleBackgroundCoverError} 
                            style={{ display: 'none' }} 
                            alt="" 
                        />
                    </>
                )}
            <div className={styles.header}>
                <h1 className={styles.title}></h1>
                <p className={styles.subtitle}>    </p>
                    
                    {/*     */}
                    <button 
                        className={styles.viewModeToggle}
                        onClick={toggleViewMode}
                        title={expandedView ? " " : " "}
                    >
                        {expandedView ? (
                            <svg viewBox="0 0 24 24" width="20" height="20" fill="currentColor">
                                <path d="M3 13h2v-2H3v2zm0 4h2v-2H3v2zm0-8h2V7H3v2zm4 4h14v-2H7v2zm0 4h14v-2H7v2zM7 7v2h14V7H7z"/>
                            </svg>
                        ) : (
                            <svg viewBox="0 0 24 24" width="20" height="20" fill="currentColor">
                                <path d="M7 14H5v5h5v-2H7v-3zm-2-4h2V7h3V5H5v5zm12 7h-3v2h5v-5h-2v3zM14 5v2h3v3h2V5h-5z"/>
                            </svg>
                        )}
                    </button>
            </div>

            {debugVisible && debugInfo && (
                <div className={styles.debugPanel}>
                    <div className={styles.debugHeader}>
                        <h3> </h3>
                        <button onClick={() => setDebugVisible(false)}></button>
                    </div>
                    <pre className={styles.debugContent}>{debugInfo}</pre>
                </div>
            )}

            <div className={styles.content}>
                    <div className={styles.tabs}>
                        <button 
                            className={`${styles.tab} ${activeTab === TabType.MyMusic ? styles.tabActive : ''}`}
                            onClick={() => setActiveTab(TabType.MyMusic)}
                        >
                             
                        </button>
                        <button 
                            className={`${styles.tab} ${activeTab === TabType.Queue ? styles.tabActive : ''}`}
                            onClick={() => setActiveTab(TabType.Queue)}
                        >
                             {queueTracks.length > 0 ? `(${queueTracks.length})` : ''}
                        </button>
                    </div>

                    <div className={styles.tabContent}>
                        {activeTab === TabType.MyMusic ? renderMyMusicTab() : renderQueueTab()}
                    </div>
                </div>

                {/*     */}
                <UploadAudio onTrackUploaded={handleTrackUploaded} />

                {/*   -   expandedMode */}
                <div className={styles.playerContainer}>
                    <div className={expandedView ? styles.playerExpanded : ''}>
                        <AuPlayerWrap expandedMode={expandedView} />
                    </div>
                </div>
            </div>
        </div>
    );
}; 