import React, { createContext, useContext, useState, useEffect } from 'react';
import { Track } from '../types/music.types';
import { api } from '../utils/api';

//     
const DEFAULT_COVER_URL = '/api/music/cover/default.png';

export interface PlayerContextProps {
    tracks: Track[];
    currentTrack: Track | null;
    currentTrackIndex: number;
    isPlaying: boolean;
    audio: HTMLAudioElement;
    repeatMode: 'none' | 'one' | 'all'; // none -  , one -  , all -  
    shuffleMode: boolean; //  
    setTracks: React.Dispatch<React.SetStateAction<Track[]>>;
    setCurrentTrack: React.Dispatch<React.SetStateAction<Track | null>>;
    setCurrentTrackIndex: React.Dispatch<React.SetStateAction<number>>;
    setIsPlaying: React.Dispatch<React.SetStateAction<boolean>>;
    playTrack: (track: Track) => void;
    playTrackByIndex: (index: number) => void;
    pauseTrack: () => void;
    nextTrack: () => void;
    prevTrack: () => void;
    togglePlay: () => void;
    toggleRepeat: () => void; //   
    toggleShuffle: () => void; //   
    setVolume: (volume: number) => void; //  
    getTrackCover: (coverUrl: string) => string; //       
    addToQueue: (track: Track) => void; //    
    removeTrackFromQueue: (trackId: number) => void; //    
}

const PlayerContext = createContext<PlayerContextProps | undefined>(undefined);

// URL API
const API_URL = process.env.REACT_APP_API_URL || 'https://blue-bikes-happen.loca.lt/api';

export const PlayerProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    console.log('[PlayerContext] Initializing PlayerContext');
    const [tracks, setTracks] = useState<Track[]>([]);
    const [currentTrack, setCurrentTrack] = useState<Track | null>(null);
    const [currentTrackIndex, setCurrentTrackIndex] = useState<number>(-1);
    const [isPlaying, setIsPlaying] = useState<boolean>(false);
    const [repeatMode, setRepeatMode] = useState<'none' | 'one' | 'all'>('none');
    const [shuffleMode, setShuffleMode] = useState<boolean>(false);
    const [shuffledIndices, setShuffledIndices] = useState<number[]>([]);
    const [audio] = useState<HTMLAudioElement>(new Audio());

    //       
    useEffect(() => {
        if (currentTrack) {
            audio.src = currentTrack.audioUrl;
            
            if (isPlaying) {
                audio.play().catch(err => {
                    console.error(' :', err);
                });
            }
            
            //   
            if (currentTrack.id > 0) {
                try {
                    api.post(`/music/${currentTrack.id}/play`, {});
                } catch (err) {
                    console.error('    :', err);
                }
            }
        }
    }, [currentTrack]);

    //  
    useEffect(() => {
        if (isPlaying) {
            audio.play().catch(err => {
                console.error('[PlayerContext]  :', err);
                setIsPlaying(false);
            });
        } else {
            audio.pause();
        }
    }, [isPlaying, audio]);

    //     -
    useEffect(() => {
        //   timeupdate    
        const handleTimeUpdate = () => {
            //        
            // console.log('[PlayerContext]  :', audio.currentTime);
        };

        const handleError = (e: Event) => {
            console.error('[PlayerContext]   :', e);
            if (audio.error) {
                console.error('[PlayerContext]  :', audio.error.code, ':', audio.error.message);
            }
        };

        const handleAbort = () => {
            console.warn('[PlayerContext]  ');
        };

        const handleStalled = () => {
            console.warn('[PlayerContext]   - ');
        };

        const handlePlay = () => {
            console.log('[PlayerContext]  ');
        };

        const handlePause = () => {
            console.log('[PlayerContext]  ');
        };

        const handleEnded = () => {
            console.log('[PlayerContext]   ( ended)');
        };

        //  
        audio.addEventListener('timeupdate', handleTimeUpdate);
        audio.addEventListener('error', handleError);
        audio.addEventListener('abort', handleAbort);
        audio.addEventListener('stalled', handleStalled);
        audio.addEventListener('play', handlePlay);
        audio.addEventListener('pause', handlePause);
        audio.addEventListener('ended', handleEnded);
        
        //    
        return () => {
            audio.removeEventListener('timeupdate', handleTimeUpdate);
            audio.removeEventListener('error', handleError);
            audio.removeEventListener('abort', handleAbort);
            audio.removeEventListener('stalled', handleStalled);
            audio.removeEventListener('play', handlePlay);
            audio.removeEventListener('pause', handlePause);
            audio.removeEventListener('ended', handleEnded);
        };
    }, [audio]);

    //       
    useEffect(() => {
        const handleEnded = () => {
            console.log('[PlayerContext]  ,   :', repeatMode);
            if (repeatMode === 'one') {
                //   
                audio.currentTime = 0;
                audio.play().catch(err => {
                    console.error('[PlayerContext]   :', err);
                });
            } else {
                //       
                console.log('[PlayerContext]       ');
                if (tracks.length > 0) {
                    //   
                    const nextIndex = (currentTrackIndex + 1) % tracks.length;
                    
                    //    
                    console.log(`[PlayerContext]   : ${nextIndex}`);
                    setCurrentTrack(tracks[nextIndex]);
                    setCurrentTrackIndex(nextIndex);
                    setIsPlaying(true);
                }
            }
        };
        
        audio.addEventListener('ended', handleEnded);
        
        return () => {
            audio.removeEventListener('ended', handleEnded);
        };
    }, [audio, repeatMode, tracks, currentTrackIndex]);

    //        
    useEffect(() => {
        if (shuffleMode && tracks.length > 0) {
            console.log('[PlayerContext]   ');
            
            //       
            const indices = Array.from({length: tracks.length}, (_, i) => i)
                .filter(i => i !== currentTrackIndex);
            
            //   ( -)
            for (let i = indices.length - 1; i > 0; i--) {
                const j = Math.floor(Math.random() * (i + 1));
                [indices[i], indices[j]] = [indices[j], indices[i]];
            }
            
            //    ,    
            if (currentTrackIndex >= 0) {
                indices.unshift(currentTrackIndex);
            }
            
            //    
            const shuffledQueue = indices.map(idx => tracks[idx]);
            
            console.log('[PlayerContext]   :', shuffledQueue.map(t => t.title));
            
            //       
            if (!shuffledIndices.length) {
                setShuffledIndices(Array.from({length: tracks.length}, (_, i) => i));
            }
            
            //    
            setTracks(shuffledQueue);
            
            //       
            if (currentTrack) {
                const newIndex = shuffledQueue.findIndex(t => t.id === currentTrack.id);
                if (newIndex !== -1) {
                    setCurrentTrackIndex(newIndex);
                }
            }
        } else if (!shuffleMode && shuffledIndices.length > 0 && tracks.length > 0) {
            //    
            console.log('[PlayerContext]    ');
            
            //       
            const originalQueue = [...tracks].sort((a, b) => {
                const indexA = shuffledIndices.findIndex(i => tracks[i].id === a.id);
                const indexB = shuffledIndices.findIndex(i => tracks[i].id === b.id);
                return indexA - indexB;
            });
            
            //    
            setTracks(originalQueue);
            
            //   
            setShuffledIndices([]);
            
            //    
            if (currentTrack) {
                const newIndex = originalQueue.findIndex(t => t.id === currentTrack.id);
                if (newIndex !== -1) {
                    setCurrentTrackIndex(newIndex);
                }
            }
        }
    }, [shuffleMode, tracks.length]);

    //     
    useEffect(() => {
        const fetchTracks = async () => {
            try {
                const data = await api.get('/music');
                console.log('[PlayerContext] Loaded tracks:', data);
                
                //    
                const tracksData = data.tracks || data;
                
                if (!tracksData || !Array.isArray(tracksData) || tracksData.length === 0) {
                    console.log('[PlayerContext] No tracks found');
                    return;
                }
                
                const validatedTracks = tracksData.map((track: any) => {
                    return {
                        id: track.id || 0,
                        title: track.title || ' ',
                        artist: track.artist || ' ',
                        duration: track.duration || '0:00',
                        coverUrl: track.coverUrl || DEFAULT_COVER_URL,
                        audioUrl: track.filename ? `${API_URL}/music/file/${track.filename}` : '',
                        playCount: track.playCount || 0
                    };
                });
                
                //    
                for (const track of validatedTracks) {
                    if (track.coverUrl !== DEFAULT_COVER_URL) {
                        //    
                        checkCoverOrientation(track.coverUrl).then(result => {
                            if (!result.isValid) {
                                console.warn(`[PlayerContext]    : ${track.title}`);
                                //     
                                setTracks(prev => 
                                    prev.map(t => 
                                        t.id === track.id ? { ...t, coverUrl: DEFAULT_COVER_URL } : t
                                    )
                                );
                            }
                        }).catch(err => {
                            console.error(`[PlayerContext]    : ${err.message}`);
                        });
                    }
                }
                
                setTracks(validatedTracks);
                
                //    
                if (validatedTracks.length > 0 && !currentTrack) {
                    console.log('[PlayerContext] Setting initial track:', validatedTracks[0]);
                    setCurrentTrack(validatedTracks[0]);
                    setCurrentTrackIndex(0);
                }
            } catch (error) {
                console.error('[PlayerContext]    :', error);
            }
        };

        fetchTracks();
    }, []);

    const playTrack = (track: Track) => {
        console.log('[PlayerContext]    :', track.title);
        
        // ,     
        const trackIndex = tracks.findIndex(t => t.id === track.id);
        
        if (trackIndex === -1) {
            //     ,  
            console.log('[PlayerContext]     , :', track.title);
            const newTracks = [...tracks, track];
            setTracks(newTracks);
            setCurrentTrack(track);
            setCurrentTrackIndex(newTracks.length - 1);
        } else {
            //      ,   
            setCurrentTrack(track);
            setCurrentTrackIndex(trackIndex);
        }
        
        setIsPlaying(true);
    };

    const playTrackByIndex = (index: number) => {
        if (index >= 0 && index < tracks.length) {
            setCurrentTrack(tracks[index]);
            setCurrentTrackIndex(index);
            setIsPlaying(true);
        }
    };

    const pauseTrack = () => {
        setIsPlaying(false);
    };

    const nextTrack = () => {
        if (tracks.length === 0) return;
        
        //       
        const nextIndex = (currentTrackIndex + 1) % tracks.length;
        console.log(`[PlayerContext]    : ${nextIndex}`);
        
        setCurrentTrack(tracks[nextIndex]);
        setCurrentTrackIndex(nextIndex);
        setIsPlaying(true);
    };

    const prevTrack = () => {
        if (tracks.length === 0) return;
        
        //       
        const prevIndex = (currentTrackIndex - 1 + tracks.length) % tracks.length;
        console.log(`[PlayerContext]    : ${prevIndex}`);
        
        setCurrentTrack(tracks[prevIndex]);
        setCurrentTrackIndex(prevIndex);
        setIsPlaying(true);
    };

    const togglePlay = () => {
        if (currentTrack) {
            setIsPlaying(!isPlaying);
        } else if (tracks.length > 0) {
            //    ,     ,  
            setCurrentTrack(tracks[0]);
            setCurrentTrackIndex(0);
            setIsPlaying(true);
        }
    };

    //   
    const toggleRepeat = () => {
        setRepeatMode(current => {
            if (current === 'none') return 'one';
            if (current === 'one') return 'all';
            return 'none';
        });
    };

    const toggleShuffle = () => {
        console.log('[PlayerContext]   ');
        setShuffleMode(current => !current);
    };

    const setVolume = (volume: number) => {
        audio.volume = Math.max(0, Math.min(1, volume));
    };

    //     URL 
    const preloadCover = (url: string): Promise<boolean> => {
        return new Promise((resolve) => {
            if (!url || url === DEFAULT_COVER_URL) {
                resolve(false);
                return;
            }
            
            const img = new Image();
            img.onload = () => resolve(true);
            img.onerror = () => resolve(false);
            img.src = url;
        });
    };

    //   URL 
    const getTrackCover = (coverUrl: string): string => {
        if (!coverUrl || coverUrl === 'undefined' || coverUrl === 'null') {
            console.warn('[PlayerContext]  URL ,  ');
            return DEFAULT_COVER_URL;
        }
        
        if (!coverUrl.startsWith('http') && !coverUrl.startsWith('/') && coverUrl != 'api/music/cover/default.png') {
            console.warn('[PlayerContext]  URL :', coverUrl);
            return DEFAULT_COVER_URL;
        }
        
        return coverUrl;
    };

    //      
    const checkCoverOrientation = (coverUrl: string): Promise<{
        isValid: boolean;
        isSquare: boolean;
        aspectRatio: number;
        width: number;
        height: number;
    }> => {
        return new Promise((resolve) => {
            const img = new Image();
            img.onload = () => {
                const width = img.naturalWidth;
                const height = img.naturalHeight;
                const aspectRatio = width / height;
                const isSquare = Math.abs(aspectRatio - 1) < 0.1; //   
                
                console.log(`[PlayerContext]  : ${width}x${height}, : ${aspectRatio.toFixed(2)}`);
                
                resolve({
                    isValid: true,
                    isSquare,
                    aspectRatio,
                    width,
                    height
                });
            };
            img.onerror = () => {
                console.warn('[PlayerContext]      :', coverUrl);
                resolve({
                    isValid: false,
                    isSquare: false,
                    aspectRatio: 0,
                    width: 0,
                    height: 0
                });
            };
            img.src = getTrackCover(coverUrl);
        });
    };

    //    ,  
    useEffect(() => {
        if (currentTrack) {
            //   ,   
            if (currentTrack.coverUrl && currentTrack.coverUrl !== DEFAULT_COVER_URL) {
                const img = new Image();
                img.onload = () => {
                    //   ,   
                };
                img.onerror = () => {
                    //   ,  
                    console.warn(`[PlayerContext]    : ${currentTrack.coverUrl}`);
                    setCurrentTrack(prev => {
                        if (prev) {
                            return { ...prev, coverUrl: DEFAULT_COVER_URL };
                        }
                        return prev;
                    });
                };
                img.src = currentTrack.coverUrl;
            }
        }
    }, [currentTrack?.id]);

    //      
    const addToQueue = (track: Track) => {
        // ,       
        const trackExists = tracks.some(t => t.id === track.id);
        
        if (!trackExists) {
            console.log('[PlayerContext]    :', track.title, track.artist);
            setTracks(prevTracks => [...prevTracks, track]);
            
            //    ,   
            if (tracks.length === 0 && !currentTrack) {
                setCurrentTrack(track);
                setCurrentTrackIndex(0);
            }
        } else {
            console.log('[PlayerContext]     :', track.title, track.artist);
        }
    };

    //      
    const removeTrackFromQueue = (trackId: number) => {
        //     
        const indexToRemove = tracks.findIndex(t => t.id === trackId);
        
        if (indexToRemove === -1) {
            console.log('[PlayerContext]     :', trackId);
            return;
        }
        
        console.log('[PlayerContext]    :', tracks[indexToRemove].title);
        
        // ,     
        const isRemovingCurrentTrack = currentTrackIndex === indexToRemove;
        
        //      
        const updatedTracks = tracks.filter((_, index) => index !== indexToRemove);
        setTracks(updatedTracks);
        
        //    
        if (isRemovingCurrentTrack) {
            if (updatedTracks.length === 0) {
                //    ,   
                setCurrentTrack(null);
                setCurrentTrackIndex(-1);
                setIsPlaying(false);
            } else {
                //    ,  
                //    ,        
                const nextIndex = Math.min(indexToRemove, updatedTracks.length - 1);
                setCurrentTrack(updatedTracks[nextIndex]);
                setCurrentTrackIndex(nextIndex);
            }
        } else {
            //   ,    ,  
            if (indexToRemove < currentTrackIndex) {
                setCurrentTrackIndex(currentTrackIndex - 1);
            }
        }
    };

    return (
        <PlayerContext.Provider
            value={{
                tracks,
                currentTrack,
                currentTrackIndex,
                isPlaying,
                audio,
                repeatMode,
                shuffleMode,
                setTracks,
                setCurrentTrack,
                setCurrentTrackIndex,
                setIsPlaying,
                playTrack,
                playTrackByIndex,
                pauseTrack,
                nextTrack,
                prevTrack,
                togglePlay,
                toggleRepeat,
                toggleShuffle,
                setVolume,
                getTrackCover,
                addToQueue,
                removeTrackFromQueue
            }}
        >
            {children}
        </PlayerContext.Provider>
    );
};

export const usePlayer = (): PlayerContextProps => {
    const context = useContext(PlayerContext);
    if (context === undefined) {
        throw new Error('usePlayer    PlayerProvider');
    }
    return context;
}; 